@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');

@layer base {
  html {
    scroll-behavior: smooth;
    overflow-x: hidden;
  }
  
  body {
    font-family: 'Inter', system-ui, sans-serif;
    overflow-x: hidden;
    width: 100%;
    position: relative;
  }

  * {
    box-sizing: border-box;
  }

  /* Prevent horizontal overflow */
  .App {
    overflow-x: hidden;
    width: 100%;
    min-height: 100vh;
  }
}

@layer components {
  .btn-primary {
    @apply bg-emergency-red hover:bg-emergency-orange text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 hover:shadow-lg;
  }
  
  .btn-secondary {
    @apply bg-trust-blue hover:bg-trust-dark text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105 hover:shadow-lg;
  }
  
  .card-hover {
    @apply transition-all duration-300 transform hover:scale-105 hover:shadow-xl;
  }

  /* Carousel optimizations */
  .carousel-container {
    will-change: transform;
    backface-visibility: hidden;
    perspective: 1000px;
  }
  
  .carousel-slide {
    will-change: opacity;
    backface-visibility: hidden;
  }

  /* Ensure images are properly scaled and visible */
  .carousel-slide img {
    min-height: 100vh;
    min-width: 100%;
    max-width: none;
  }

  /* Professional text overlay styles */
  .text-overlay-professional {
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    letter-spacing: 0.025em;
  }

  .text-shadow-strong {
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8), 1px 1px 2px rgba(0, 0, 0, 0.9);
  }

  /* Navbar mobile fixes */
  .navbar-mobile-fix {
    min-width: 0;
    overflow: hidden;
  }

  .navbar-mobile-fix .logo {
    min-width: 0;
    flex: 1;
  }

  .navbar-mobile-fix .mobile-buttons {
    flex-shrink: 0;
  }

  /* Ensure proper mobile spacing */
  @media (max-width: 640px) {
    .navbar-container {
      padding-left: 12px;
      padding-right: 12px;
    }
  }
}
